
.loader
{
    text-align: center;
}

.loader img
{
    max-height: 0;
    margin: 0;
    -webkit-transition: 1s all ease;
    -o-transition: 1s all ease;
    transition: 1s all ease;
}

.loader.active img
{
    max-height: 50px;
    margin: 10px auto 20px;
}


